 .section-subcribe{
     display: flex;
     margin:auto;
     background-color: var(--color-dark);
     padding: 62px 20px 62px 20px;
    background-image: linear-gradient(0deg, #0003 0%, #0003 100%), url('../images/subscribe/subcribe-bg-mobile@1x.jpg');
     min-width: 375px;
     height: 370px;


     background-repeat: no-repeat;
     background-position: center;
     background-size: cover;



 }

 @media (min-device-pixel-ratio: 2),
 (min-resolution: 192dpi),
 (min-resolution: 2dppx) {
     .section-subscribe {
background-image: linear-gradient(rgba(46, 47, 66, 0.7), rgba(46, 47, 66, 0.7)), url('../images/subscribe/subcribe-bg-mobile@2x.jpg');
     }

 }
.subscribe{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 24

}
.subscribe-button{
    display: inline-flex;
    padding: 14px 40px;
    align-items: flex-start;
    gap: 14px;
    border-radius: 100px;
    border: none;
    color: var(--white-color, #FFF);
    background-color: var(--carrot-orange-color,#FD9222);
    transition: background-color 300ms cubic-bezier(0.4, 0, 0.2, 1),
                color 300ms cubic-bezier(0.4, 0, 0.2, 1) ;
}

.subscribe-button:hover,
.subscribe-button:focus{
    cursor: pointer;
    background-color: var(--linen-color, #fcf1e0);
    color: var(--carrot-orange-color, #FD9222);
}   


.subscribe-button-font{
    
    font-family: Montserrat;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}
.subscribe-h-font{
    color: var(--white-color, #FFF);
    text-align: center;
    font-family: Montserrat;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 1.2;
    letter-spacing: -0.6px;
    text-transform: uppercase;
    width: 291px;

}
.subscribe-p-font{
    color: #FFF;
    text-align: center;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.286;
    letter-spacing: -0.28px;
    width: 326px;;
}



